import uniIcon from "@/components/uni-icons/uni-icons.vue";
const JsyServer = require("services/jsy-server.js");
const Api = require('services/config/api.js');
export default {
	components:{
		uniIcon
	},
	data() {
		return {
			contPhone: '',
			contPass: '',
			passType: 'password',
			isPhoneClear: false,
			isPassClear: false
		};
	},

	
	methods:{
		getPupDefault:function(){
			JsyServer.pupDefault().then(res => {
				    if (res.data.status == 0){
						console.log(res.data.data.msg)
						uni.setStorageSync("pupDefault",res.data.data.msg);
						
					}else {
						uni.showToast({
							title: '没有认领身份，请联系帮办',
							icon: 'none',
							duration: 2000
						});
					
					}
						
				}).catch(err => {
				  console.log("currentIdentity=err==", res);
				});
		},

		showPass: function(){
			this.passType = this.passType==='password'?'text':'password'
		},
		clearPhone: function(){
			this.contPhone = ''
		},
		clearPass: function(){
			this.contPass =''
		},
		showCloseIcon:function(e){
			
			if (e.target.value) {
				this.isPhoneClear = true
			}
			else {
				this.isPhoneClear = false
			}
		},
		showPassClearIcon: function(e){
			if (e.target.value){
				this.isPassClear = true
			}else{
				this.isPassClear = false
			}
			
		},
		examineCount:function(){
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone))){
				uni.showToast({
					title: '请输入正确电话号码！',
					icon: 'none',
					duration: 2000
				});
				return ;
		    }
		},
		
		formSubmit:function(e){
			let data = e.detail.value;
			console.log(data);
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(data.contPhone))){
				uni.showToast({
					title: '请输入正确电话号码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if(!data.contPass || data.contPass.length<6){
				uni.showToast({
					title: '请输入至少六位密码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			uni.showLoading({
				title: '加载中。。',
				mask: true
			});
			uni.request({
				url: Api.login,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					phone: data.contPhone,
					password: data.contPass
				},
				success: res => {
					console.log(res.data);
					if (res.data.status=== 0){
						uni.setStorageSync("token",res.data.data.msg);
						this.getPupDefault()
						setTimeout(function() {
							if (uni.getStorageSync('pupDefault')){
								uni.switchTab({
									url:"/pages/qing-f-c/index"
								})	
							}
						}, 500);
						
							
					}else {
						return
					}
					if (res.data.status === 1){
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 3000
						});
					}
				},
				fail: (err) => {
					console.log(err.data)
				},
				complete: () => {
					uni.hideLoading();
				}
			});
			// options: {
			// 	url: this.apiServer+'/ul/login',
			// 	method: 'POST',
			// 	data: {
			// 		phone: data.contPhone,
			// 		password: data.contPass
			// 	}
			// };
			// this.http(this.options).then(res =>{
			// 	console.log(res)
			// }).catch(err=> {
			// 	console.log(err)
			// });
		}
		
	}
}
